import { Component, OnInit ,EventEmitter, Output} from '@angular/core';
import { FormGroup, FormBuilder, FormControl, Validators } from '@angular/forms';
import { Message } from 'primeng/primeng';
import { PerssionManageHttp } from '../../http/perssion-manage.http.service';
import { BusinessPermitManageBean } from '../bean/business-permit-manage.bean';

@Component({
  selector: 'business-permit-add',
  templateUrl: './business-permit-add.component.html',
  styleUrls: ['./business-permit-add.component.scss']
})
export class BusinessPermitAddComponent implements OnInit {
  @Output() outValue:EventEmitter<any> =new EventEmitter();
  businessPermitManageBean:BusinessPermitManageBean=new BusinessPermitManageBean();
  userform: FormGroup; //校验
  //提示信息
  msgs: Message[] = [];
  constructor(
    public fb: FormBuilder,
    public httpService: PerssionManageHttp
  ) { }

  ngOnInit() {
    this.userform = this.fb.group({
      'permissionsId': new FormControl('', [Validators.pattern(/^[\w]{1,20}$/), Validators.required]),
      // 'permissionsDescription': new FormControl('', [Validators.pattern(/^([\u4E00-\u9FA5\(\)\（\）]){1,60}$/), Validators.required]),
    });
  }
  preserveClick() {
    //校验的
    for (const i in this.userform.controls) {
      this.userform.controls[i].markAsDirty();
    }
    //处理是否提交
    for (const i in this.userform.controls) {
      if (!this.userform.controls[i].valid) {
        return;
      }
    }
    this.businessPermitManageBean.permissionType='BUSINESS';
    this.httpService.createPermissions(this.businessPermitManageBean).subscribe(data => {
      if (data.returnCode.code == "success") {
        this.outValue.emit({ msgs: '新增成功', display: false });
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
    })
  }
  modifyCloseBtn() {
    this.outValue.emit({ display: false })
  }

}
