import { Component, OnInit, ViewChild, ComponentRef } from '@angular/core';
import { ConfirmationService, Message } from 'primeng/primeng';
import { BusinessPermitManageBean } from './bean/business-permit-manage.bean';
import { PerssionManageHttp } from '../http/perssion-manage.http.service';
import { Commfunc } from '../../../../../../@uisftech/common/service/commonFun';
@Component({
  selector: 'business-permit-manage',
  templateUrl: './business-permit-manage.component.html',
  styleUrls: ['./business-permit-manage.component.scss']
})
export class BusinessPermitManageComponent implements OnInit {
  @ViewChild("powerList") powesrList: ComponentRef<any>;
  permissionsId: any;
  total: any;
  tableData: any;
  first: number = 0;
  businessPermitManageBean: BusinessPermitManageBean = new BusinessPermitManageBean();
  headerTitle: string;
  display: boolean;//弹框
  updateValue: string;//修改传值
  showModel: string;
  //提示信息
  msgs: Message[] = [];
  inpFlag: boolean = true;
  powerInValue:any;
  constructor(
    public confirmationService: ConfirmationService,
    public httpService: PerssionManageHttp,
    public commfunc: Commfunc
  ) { }

  ngOnInit() {
    this.businessPermitManageBean.pageNum = 1;
    this.businessPermitManageBean.pageSize = 10;
    this.orgquery('');
    this.queryClick();
  }
  inpquery(event) {
    if (this.inpFlag) {
      this.inpFlag = false;
      setTimeout(() => {
        this.inpFlag = true;
        let permissionsId = event.target.value;
        this.orgquery(permissionsId);
      }, 1000)
    }
  }

  //机构模糊查询
  orgquery(permissionsId) {
    let param = {
      permissionsId,
      pageSize: 30,
      permissionType:"BUSINESS"
    }
    this.httpService.querySecurityPermissionList(param).subscribe(data => {
      if (data.permissionList) {
        this.permissionsId = this.commfunc.listToLabelvalue(data.permissionList, 'permissionsId', 'permissionsId');
      }
    })
  }

  //查询
  query() {
    this.businessPermitManageBean.permissionType="BUSINESS";
    this.httpService.querySecurityPermissionList(this.businessPermitManageBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.tableData = data.permissionList ? data.permissionList : [];
        this.total = data.total;
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }
  //点击查询
  queryClick() {
    this.first = 0;
    this.businessPermitManageBean.pageNum = 1;
    this.businessPermitManageBean.pageSize = 10;
    this.query();
  }
  //重置
  resetClick() {
    this.businessPermitManageBean.permissionsId = '';
    this.businessPermitManageBean.permissionsDescription = '';
    this.first=0;
    this.businessPermitManageBean.pageNum = 1;
    this.businessPermitManageBean.pageSize = 10;
    this.query();
  }
  // 分配资源
  powerList(item) {
    this.headerTitle = '功能列表';
    this.display = true;
    this.showModel = '3';
    this.powerInValue = item;
    setTimeout(() => {
      this.powerInValue = null;
    }
      , 200)
  }
  // 分配资源回调
  powerListCall(param) {
    this.display = param;
    this.query()
  }
  //新增
  add() {
    this.headerTitle = '用户审批权限管理新增';
    this.display = true;
    this.showModel = '1';
  }
  //新增回调
  addCall(event) {
    if (event.msgs) {
      this.msgs = [];
      this.msgs.push({ severity: 'success', summary: '提示', detail: event.msgs });
      this.query()
    }
    this.display = event.display;
  }
  //修改
  update(item) {
    this.headerTitle = '用户审批权限管理修改';
    this.display = true;
    this.showModel = '2';
    this.updateValue = item;
  }
  //修改回调
  updateCall(event) {
    if (event.msgs) {
      this.msgs = [];
      this.msgs.push({ severity: 'success', summary: '提示', detail: event.msgs });
      this.query()
    }
    this.display = event.display;
  }
  //删除
  delete(item) {
    this.confirmationService.confirm({
      message: '确认要删除吗?',
      accept: () => {
        // 条件
        let param = { permissionsId: item.permissionsId };
        this.httpService.deletePermissions(param).subscribe(data => {
          if (data.returnCode.code == "success") {
            this.msgs = [];
            this.msgs.push({ severity: 'success', summary: '提示', detail: '删除成功！' });
            this.query();
          } else {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
          }
        }, error => {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: "删除失败" });
        })
      }
    });
  }
  //分页
  paginate(event) {
    let rows = event.rows;
    //每页显示的条数
    this.businessPermitManageBean.pageSize = rows;
    //当前页
    let currentPage = event.page + 1;
    this.businessPermitManageBean.pageNum = currentPage;
    this.first = event.rows * event.page;

    //调用查询的方法
    this.query();
  }

  // 关闭模态框
  hide() {
    this.display = false;
    this.showModel = undefined;
  }
}
